<div class="hello"><div class="ui container"><img id="logo" alt="Logo" src="../assets/logo.png"><h2 class="center aligned ui header">自學AI<div class="center aligned ui sub header">以自學問答集為基礎，提供更即時的回答</div></h2><div class="ui input"><input autofocus type="text" placeholder="問AI關於自學的任何問題..." v-model="message" @keyup.enter="sendMessage"><button class="ui primary button" @click="sendMessage">送出</button></div><div class="result"><p v-if="result === '' &amp;&amp; message !== '' &amp;&amp; isLoading">載入中，請稍候...</p><div v-else-if="result !== ''"><p>{{ parseResult(result) }}</p><br><br><p>您覺得這個回答怎麼樣呢？</p><div class="ui buttons"><button class="ui green button" @click="sendFeedback('good')"><i class="thumbs up icon"></i>很棒</button><button class="ui red button" @click="sendFeedback('bad')"><i class="thumbs down icon"></i>不佳</button></div></div></div></div></div>